<template>
    <main>
        <div id="wrapper">
            <header>
                <div>Документация Activae Vitae</div>
            </header>
            <nav>
                <div class="elementInList" v-for="element in elementsList" v-bind:key="element.id">
                    <div
                        v-on:click="setValuesForDocumentation(element)">Метод {{ element.name }}</div>
                </div>
            </nav>

            <section>
                <div class="aboutElement">
                    <div v-if="activeElement" class="elementName"> {{ activeElement.name }}</div>
                    <div v-if="activeElement" class="elementInfo">
                        {{ activeElement.description }}
                        <br /><br />
                        <div v-if="activeElement.example">{{ activeElement.example }}</div>
                    </div>

                </div>
                <br /><br />
                <div class="funnyThings">Some funny things</div>
            </section>

            <aside>
                Some fast addresses
            </aside>
        </div>
    </main>
</template>

<script>
import docs from "./docs.js"


function translitaration(exampleObject) {
    let send = "Params to SEND:\n";
    let response = "Params in RESPONSE:\n";
    if (!exampleObject) return "";

    for (const [key, value] of Object.entries(exampleObject.send)) {
        send += "  " + key + ": " + JSON.stringify(value) + "\n"
    }
    for (const [key, value] of Object.entries(exampleObject.response)) {
        response += "  " + key + ": " + JSON.stringify(value) + "\n"
    }
    return send + "\n\n\n" + response;
}


export default {
    name: 'App',
    data() {
        return {
            elementsList: docs.documenation,
            activeElement: null,
            docsExample: docs.examples,
            translitaration: translitaration
        }
    },
    methods: {
        setValuesForDocumentation(selectedObject) {
            let description = selectedObject.description;
            let newObject = selectedObject;

            description = description.replace("\n", "<br />");
            newObject.description = description;

            newObject.example = this.translitaration(this.docsExample[newObject.name]);

            this.activeElement = newObject;
            return null;
        }
    }
}
</script>

<style src="./index.css"></style>
